const config = {
	breakpoint: {
		mobile: 425,
		tablet: 768,
		laptop: 992,
		desktop: 1280,
		large: 1366,
	},
};

const loader = () => {
	const img = import.meta.globEager('../assets/images/**/*.{jpg,jpeg,png,gif,svg,webp}');
	const sourses = Object.values(img).map((mod) => mod.default);
	sourses.forEach((src) => {
		new URL(String(src), import.meta.url);
	});
};

export { config, loader };
