import { config, loader } from '../config/config';
import { disableScroll, enableScroll, getDeviceType } from '../config/utilities';

import { Lazy } from '../components/lazy/lazy';
import { Tabs } from '../components/tabs/tabs';
import { Collapse } from '../components/collapse/collapse';

import { header } from '../components/header/header';

window.app = {};
window.config = config;

window.app.disableScroll = disableScroll;
window.app.enableScroll = enableScroll;
window.app.getDeviceType = getDeviceType;

window.app.Lazy = Lazy;
window.app.Tabs = Tabs;
window.app.Collapse = Collapse;

loader();

window.addEventListener('DOMContentLoaded', () => {
	header();

	document.querySelectorAll('.js-lazy').forEach((element) => {
		new app.Lazy(element);
	});

	document.querySelectorAll('.js-tabs').forEach((element) => {
		new app.Tabs(element);
	});

	document.querySelectorAll('.js-collapse').forEach((element) => {
		new app.Collapse(element);
	});
});
